import React, { useState, useEffect } from 'react';
import {
    Card,
    CardContent,
    Typography,
    Grid,
    Box,
    Chip,
    Divider,
    Skeleton,
    Paper
} from '@mui/material';
import {
    WbTwilight as WbTwilightIcon,
    WbSunny as WbSunnyIcon,
    Brightness4 as Brightness4Icon,
    Brightness7 as Brightness7Icon,
    NightsStay as NightsStayIcon
} from '@mui/icons-material';
import { formatTime12Hour } from '../../utils/formatters';

const PrayerTimesCard = ({ prayerTimes, loading }) => {
    const [currentTime, setCurrentTime] = useState(new Date());
    const [nextPrayer, setNextPrayer] = useState(null);

    // Update current time every minute
    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(new Date());
        }, 60000);

        return () => clearInterval(timer);
    }, []);

    // Determine next prayer time
    useEffect(() => {
        if (!prayerTimes) return;

        const now = currentTime;
        const currentHour = now.getHours();
        const currentMinute = now.getMinutes();

        // Define prayer times to check (exclude Sunrise and Sunset)
        const prayers = [
            { name: 'Fajr', time: prayerTimes.Fajr },
            { name: 'Dhuhr', time: prayerTimes.Dhuhr },
            { name: 'Asr', time: prayerTimes.Asr },
            { name: 'Maghrib', time: prayerTimes.Maghrib },
            { name: 'Isha', time: prayerTimes.Isha }
        ];

        // Find the next prayer
        let next = null;

        for (const prayer of prayers) {
            const [prayerHour, prayerMinute] = prayer.time.split(':').map(Number);

            if (
                prayerHour > currentHour ||
                (prayerHour === currentHour && prayerMinute > currentMinute)
            ) {
                next = prayer;
                break;
            }
        }

        // If we've passed all prayers, next is Fajr tomorrow
        if (!next) {
            next = { name: 'Fajr (Tomorrow)', time: prayerTimes.Fajr };
        }

        setNextPrayer(next);
    }, [prayerTimes, currentTime]);

    // Get prayer icon based on name
    const getPrayerIcon = (prayerName) => {
        switch (prayerName) {
            case 'Fajr':
                return <WbTwilightIcon sx={{ color: '#1976d2' }} />;
            case 'Sunrise':
                return <WbSunnyIcon sx={{ color: '#ff9800' }} />;
            case 'Dhuhr':
                return <Brightness7Icon sx={{ color: '#ff9800' }} />;
            case 'Asr':
                return <Brightness4Icon sx={{ color: '#f57c00' }} />;
            case 'Maghrib':
                return <WbTwilightIcon sx={{ color: '#e65100' }} />;
            case 'Isha':
                return <NightsStayIcon sx={{ color: '#303f9f' }} />;
            case 'Midnight':
                return <NightsStayIcon sx={{ color: '#000' }} />;
            default:
                return <WbTwilightIcon />;
        }
    };

    // Check if a prayer time has passed
    const isPrayerTimePassed = (timeStr) => {
        if (!timeStr) return false;

        const now = currentTime;
        const currentHour = now.getHours();
        const currentMinute = now.getMinutes();

        const [prayerHour, prayerMinute] = timeStr.split(':').map(Number);

        return (
            currentHour > prayerHour ||
            (currentHour === prayerHour && currentMinute >= prayerMinute)
        );
    };

    // If loading, show skeleton
    if (loading) {
        return (
            <Card elevation={2}>
                <CardContent>
                    <Skeleton variant="text" width="50%" height={40} />
                    <Skeleton variant="text" width="30%" height={20} sx={{ mb: 2 }} />

                    {[1, 2, 3, 4, 5].map((i) => (
                        <Box key={i} sx={{ display: 'flex', mb: 2 }}>
                            <Skeleton variant="circular" width={40} height={40} sx={{ mr: 2 }} />
                            <Box width="100%">
                                <Skeleton variant="text" width="25%" height={24} />
                                <Skeleton variant="text" width="15%" height={20} />
                            </Box>
                        </Box>
                    ))}
                </CardContent>
            </Card>
        );
    }

    // If no prayer times, show empty message
    if (!prayerTimes) {
        return (
            <Paper sx={{ p: 3, textAlign: 'center' }}>
                <Typography variant="h6" color="text.secondary">
                    No prayer times to display
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                    Please select a location to view prayer times.
                </Typography>
            </Paper>
        );
    }

    // Main component
    return (
        <Card elevation={2}>
            <CardContent>
                <Box sx={{ mb: 2 }}>
                    <Typography variant="h5" color="primary" gutterBottom>
                        Prayer Times
                    </Typography>

                    {nextPrayer && (
                        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                            <Typography variant="subtitle1" sx={{ mr: 1 }}>
                                Next Prayer:
                            </Typography>
                            <Chip
                                label={`${nextPrayer.name} - ${formatTime12Hour(nextPrayer.time)}`}
                                color="secondary"
                                sx={{ fontWeight: 'medium' }}
                            />
                        </Box>
                    )}
                </Box>

                <Divider sx={{ my: 2 }} />

                <Grid container spacing={2}>
                    {/* Filter out some prayer times if desired */}
                    {Object.entries(prayerTimes)
                        .filter(([name]) => name !== 'Imsak' && name !== 'Sunset') // Filter out some prayers
                        .map(([name, time]) => {
                            const isPassed = isPrayerTimePassed(time);
                            const isNext = nextPrayer?.name === name;

                            return (
                                <Grid item xs={12} sm={6} md={4} key={name}>
                                    <Box
                                        sx={{
                                            p: 2,
                                            display: 'flex',
                                            alignItems: 'center',
                                            borderRadius: 1,
                                            bgcolor: isNext ? 'rgba(212, 175, 55, 0.1)' : 'transparent',
                                            border: isNext ? '1px solid' : '1px solid',
                                            borderColor: isNext ? 'secondary.main' : 'divider',
                                            position: 'relative',
                                            transition: 'all 0.3s ease',
                                            height: 100, // Fixed height for all cards
                                            overflow: 'hidden', // Handle overflow content
                                            '&:hover': {
                                                boxShadow: 1,
                                                transform: 'translateY(-2px)'
                                            }
                                        }}
                                    >
                                        <Box
                                            sx={{
                                                width: 40,
                                                height: 40,
                                                minWidth: 40, // Ensure the icon area doesn't shrink
                                                borderRadius: '50%',
                                                display: 'flex',
                                                alignItems: 'center',
                                                justifyContent: 'center',
                                                bgcolor: isPassed ? 'rgba(0,0,0,0.04)' : 'rgba(0,0,0,0.02)',
                                                mr: 2
                                            }}
                                        >
                                            {getPrayerIcon(name)}
                                        </Box>
                                        <Box sx={{ width: '100%' }}> {/* Ensure text container takes remaining width */}
                                            <Typography
                                                variant="subtitle1"
                                                component="div"
                                                sx={{
                                                    fontWeight: isNext ? 700 : 500,
                                                    color: isPassed ? 'text.secondary' : 'text.primary',
                                                    display: 'flex',
                                                    alignItems: 'center',
                                                    flexWrap: 'wrap', // Allow the label to wrap if needed
                                                    lineHeight: 1.3 // Tighten line height
                                                }}
                                            >
                                                {name}
                                                {isNext && (
                                                    <Chip
                                                        size="small"
                                                        label="Next"
                                                        sx={{
                                                            ml: 1,
                                                            bgcolor: 'secondary.main',
                                                            color: 'white',
                                                            height: 20,
                                                            fontSize: '0.7rem'
                                                        }}
                                                    />
                                                )}
                                            </Typography>
                                            <Typography
                                                variant="h6"
                                                component="div"
                                                sx={{
                                                    color: isPassed
                                                        ? 'text.secondary'
                                                        : isNext
                                                            ? 'secondary.main'
                                                            : 'primary.main',
                                                    fontWeight: isNext ? 700 : 500,
                                                    mt: 0.5 // Add a bit more space between label and time
                                                }}
                                            >
                                                {formatTime12Hour(time)}
                                            </Typography>
                                        </Box>
                                    </Box>
                                </Grid>
                            );
                        })}
                </Grid>
            </CardContent>
        </Card>
    );
};

export default PrayerTimesCard;