import React, { useRef, useEffect, useState } from 'react';
import BauhausClockSVG from './BauhausClockSVG';

const useResponsiveSize = (max = 600, min = 200) => {
  const ref = useRef();
  const [size, setSize] = useState(max);

  useEffect(() => {
    const handleResize = () => {
      if (ref.current) {
        const width = ref.current.offsetWidth;
        setSize(Math.max(min, Math.min(width, max)));
      }
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [max, min]);

  return [ref, size];
};

const ResponsivePrayerClockBauhaus = ({ max = 600, min = 200, theme = 'system' }) => {
  const [containerRef, size] = useResponsiveSize(max, min);
  return (
    <div ref={containerRef} style={{ width: '100%', maxWidth: max, margin: '0 auto' }}>
      <BauhausClockSVG size={size} theme={theme} />
    </div>
  );
};

export default ResponsivePrayerClockBauhaus;
