import React, { useRef, useEffect, useState } from 'react';

const THEMES = {
  day: {
    face: '#fff',
    marker: '#bfa13a', // gold for markers
    hourHand: '#14532d', // dark green for hour hand
    minuteHand: '#14532d', // dark green for minute hand
    secondHand: '#bfa13a', // gold for second hand
    center: '#bfa13a', // gold for center dot
    shadow: '#eee',
  },
  night: {
    face: '#222',
    marker: '#bfa13a', // gold for markers
    hourHand: '#bfa13a', // gold for hour hand
    minuteHand: '#14532d', // dark green for minute hand
    secondHand: '#bfa13a', // gold for second hand
    center: '#bfa13a', // gold for center dot
    shadow: '#111',
  },
};

const PRAYER_TIMES = [
  { name: 'Fajr', angle: -90 },
  { name: 'Sunrise', angle: -60 },
  { name: 'Dhuhr', angle: 0 },
  { name: 'Asr', angle: 60 },
  { name: 'Maghrib', angle: 120 },
  { name: 'Isha', angle: 180 },
];

const PILGRIM_COUNT = 18;

function getSystemTheme() {
  if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
    return 'night';
  }
  return 'day';
}

const BauhausClockSVG = ({ size = 400, theme = 'system', prayerTimes = PRAYER_TIMES }) => {
  const [now, setNow] = useState(new Date());
  // Set default theme to 'day' (light) if theme is 'system'
  const [currentTheme, setCurrentTheme] = useState(theme === 'system' ? 'day' : theme);

  // Pilgrims animation state
  const [pilgrimAngles, setPilgrimAngles] = useState(
    Array.from({ length: PILGRIM_COUNT }, (_, i) => (i / PILGRIM_COUNT) * 2 * Math.PI)
  );

  useEffect(() => {
    let frame;
    const update = () => {
      setNow(new Date());
      frame = requestAnimationFrame(update);
    };
    frame = requestAnimationFrame(update);
    return () => cancelAnimationFrame(frame);
  }, []);

  useEffect(() => {
    if (theme === 'system') {
      // Do not auto-detect system theme, always use 'day' (light)
      setCurrentTheme('day');
      return;
    }
    setCurrentTheme(theme);
  }, [theme]);

  useEffect(() => {
    let frame;
    const animate = () => {
      setPilgrimAngles((prev) => prev.map((a, i) => (a - 0.008 - i * 0.0005 + 2 * Math.PI) % (2 * Math.PI)));
      frame = requestAnimationFrame(animate);
    };
    frame = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(frame);
  }, []);

  const t = THEMES[currentTheme] || THEMES.day;
  const radius = size / 2;
  const center = radius;
  const hour = now.getHours() % 12;
  const minute = now.getMinutes();
  const second = now.getSeconds();
  const ms = now.getMilliseconds();

  // Smooth hand angles
  const hourAngle = ((hour + minute / 60) / 12) * 360;
  const minuteAngle = ((minute + (second + ms / 1000) / 60) / 60) * 360;
  const secondAngle = ((second + ms / 1000) / 60) * 360;

  // Hand lengths
  const hourLen = radius * 0.45;
  const minLen = radius * 0.7;
  const secLen = radius * 0.8;

  // Hand widths
  const hourW = 8, minW = 5, secW = 2.5;

  // Markers
  const markers = Array.from({ length: 12 }).map((_, i) => {
    const angle = (i * 30 - 90) * Math.PI / 180;
    const x1 = center + Math.cos(angle) * (radius * 0.82);
    const y1 = center + Math.sin(angle) * (radius * 0.82);
    const x2 = center + Math.cos(angle) * (radius * 0.9);
    const y2 = center + Math.sin(angle) * (radius * 0.9);
    return <line key={i} x1={x1} y1={y1} x2={x2} y2={y2} stroke={t.marker} strokeWidth={4} />;
  });

  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
      {/* Theme Switcher */}
      <div style={{ marginBottom: 32 }}>
        <button onClick={() => setCurrentTheme('day')}>Day</button>
        <button onClick={() => setCurrentTheme('night')}>Night</button>
        <button onClick={() => setCurrentTheme(getSystemTheme())}>System</button>
      </div>
      <svg width={size} height={size} style={{ display: 'block', overflow: 'visible' }} viewBox={`0 0 ${size} ${size}`}>
        {/* Face */}
        <circle cx={center} cy={center} r={radius * 0.95} fill={t.face} stroke={t.marker} strokeWidth={3} />
        {/* Markers */}
        {markers}
        {/* Big, modern hour numbers inside the clock */}
        {Array.from({ length: 12 }).map((_, i) => {
          const hour = i === 0 ? 12 : i;
          const angle = (i * 30 - 90) * Math.PI / 180;
          // Move hours further out by increasing hourRadius
          const hourRadius = radius * 0.66; // increased from 0.56
          const x = center + Math.cos(angle) * hourRadius;
          const y = center + Math.sin(angle) * hourRadius + size * 0.025;
          return (
            <text
              key={hour}
              x={x}
              y={y}
              textAnchor="middle"
              alignmentBaseline="middle"
              fontSize={size * 0.09}
              fontWeight="900"
              fill="#14532d" // dark green for hours
              style={{
                fontFamily: '"Switzer", "Montserrat", "Arial Black", Arial, sans-serif',
                letterSpacing: '-2px',
                textShadow: '0 2px 8px #fff, 0 1px 0 #bbb',
                userSelect: 'none',
                pointerEvents: 'none',
              }}
            >
              {hour}
            </text>
          );
        })}
        {/* Modern minute numbers outside the clock */}
        {[5,10,15,20,25,30,35,40,45,50,55,60].map((minute, i) => {
          const angle = (minute * 6 - 90) * Math.PI / 180;
          const minRadius = radius * 1.13;
          const x = center + Math.cos(angle) * minRadius;
          const y = center + Math.sin(angle) * minRadius + size * 0.02;
          return (
            <text
              key={minute}
              x={x}
              y={y}
              textAnchor="middle"
              alignmentBaseline="middle"
              fontSize={size * 0.06}
              fontWeight={700}
              fill="#bfa13a" // gold for minutes
              style={{
                fontFamily: '"Switzer", "Montserrat", "Arial Black", Arial, sans-serif',
                letterSpacing: '-1px',
                textShadow: '0 2px 8px #fff',
                userSelect: 'none',
                pointerEvents: 'none',
              }}
            >
              {minute}
            </text>
          );
        })}
        {/* Pilgrims (animated circles) */}
        {pilgrimAngles.map((a, i) => {
          const pilgrimRadius = radius * 0.38;
          const px = center + Math.cos(a) * pilgrimRadius;
          const py = center + Math.sin(a) * pilgrimRadius;
          return (
            <circle
              key={i}
              cx={px}
              cy={py}
              r={size * 0.018}
              fill={i % 2 === 0 ? '#fff' : '#e0e0e0'}
              stroke="#aaa"
              strokeWidth={1}
              opacity={0.95}
            />
          );
        })}
        {/* 2D Kaaba in the center */}
        <g>
          {/* Kaaba base */}
          <rect
            x={center - size * 0.07}
            y={center - size * 0.07}
            width={size * 0.14}
            height={size * 0.14}
            fill="#111"
            stroke="#333"
            strokeWidth={2}
            rx={size * 0.012}
          />
          {/* Gold band */}
          <rect
            x={center - size * 0.07}
            y={center - size * 0.03}
            width={size * 0.14}
            height={size * 0.025}
            fill="#e4c04d"
          />
          {/* Door */}
          <rect
            x={center - size * 0.022}
            y={center + size * 0.025}
            width={size * 0.044}
            height={size * 0.045}
            fill="#e4c04d"
            stroke="#bfa13a"
            strokeWidth={1}
            rx={size * 0.006}
          />
        </g>
        {/* Hour hand */}
        <rect
          x={center - hourW / 2}
          y={center - hourLen * 0.85}
          width={hourW}
          height={hourLen * 0.85}
          rx={hourW / 2}
          fill={t.hourHand}
          stroke="#B9BEBF"
          strokeWidth={1.5}
          style={{
            transform: `rotate(${hourAngle}deg)`,
            transformOrigin: `${center}px ${center}px`,
            filter: 'drop-shadow(0 2px 2px rgba(0,0,0,0.08))',
          }}
        />
        {/* Minute hand */}
        <rect
          x={center - minW / 2}
          y={center - minLen * 0.98}
          width={minW}
          height={minLen * 0.98}
          rx={minW / 2}
          fill={t.minuteHand}
          stroke="#B9BEBF"
          strokeWidth={1}
          style={{
            transform: `rotate(${minuteAngle}deg)`,
            transformOrigin: `${center}px ${center}px`,
            filter: 'drop-shadow(0 2px 2px rgba(0,0,0,0.08))',
          }}
        />
        {/* Second hand */}
        <g style={{
          transform: `rotate(${secondAngle}deg)`,
          transformOrigin: `${center}px ${center}px`,
        }}>
          <rect
            x={center - secW / 2}
            y={center - secLen * 0.98}
            width={secW}
            height={secLen * 0.98}
            rx={secW / 2}
            fill={t.secondHand}
            stroke="#B9BEBF"
            strokeWidth={0.5}
          />
          <circle
            cx={center}
            cy={center - secLen * 0.98}
            r={secW * 1.5}
            fill={t.secondHand}
            opacity={0.8}
          />
        </g>
        {/* Center dot */}
        <circle cx={center} cy={center} r={10} fill={t.center} />
      </svg>
      {/* Overlay below the clock for next prayer and location */}
      {typeof window !== 'undefined' && window.nextPrayerName && window.nextPrayerTime && window.cityName && (
        <div style={{
          marginTop: 24,
          textAlign: 'center',
          fontSize: size * 0.06,
          fontWeight: 700,
          color: t.marker,
          letterSpacing: '0.5px',
        }}>
          Next: {window.nextPrayerName} at {window.nextPrayerTime}<br />
          <span style={{ fontSize: size * 0.035, color: '#14532d', fontWeight: 500 }}>{window.cityName}</span>
        </div>
      )}
    </div>
  );
};

export default BauhausClockSVG;
